<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">
          <div class="text-center text-muted mb-4">
            <small>Sign in with credentials</small>
          </div>
          <form role="form" @submit.prevent.stop="track" novalidate>
            <base-input
              :class="['input-group-alternative mb-3', { 'is-invalid': errors.code }]"
              placeholder="Tracking code"
              addon-left-icon="ni ni-bag-17"
              v-model="code"
              :disabled="loading || success"
              :error="errors.code"
            ></base-input>

            <base-checkbox
              class="custom-control-alternative"
              :disabled="loading || success"
              v-model="remember"
            >
              <span class="text-muted">Remember my code</span>
            </base-checkbox>
            <div class="text-center">
              <base-button
                type="primary"
                nativeType="submit"
                class="my-4"
                :disabled="loading || success"
                block
              >Track My Packages</base-button>
            </div>
          </form>
        </div>
        <base-progress
          v-show="loading"
          :height="6"
          :show-percentage="false"
          :value="100"
          striped
          animated
        />
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { validateCode } from "../utils/validate";
export default {
  name: "login",
  data: () => {
    return {
      code: "",
      loading: false,
      remember: true,
      success: false,
      errors: {}
    };
  },
  methods: {
    async track() {
      this.errors = {};
      this.$notifications.clear();

      const validCode = validateCode(this.code);
      if (!validCode.valid) {
        Vue.set(this.errors, "code", validCode.error);
        return;
      }

      this.loading = true;
      return await this.$store
        .dispatch("track", {
          code: this.code,
          remember: this.remember
        })
        .then(success => {
          this.success = success;
          this.$notify({
            message: "Your packages have been loaded successfully, redirecting...",
            type: "success",
            timeout: 3000
          });
          setTimeout(() => this.$router.push({ name: "dashboard" }), 3000);
        })
        .catch(error => {
          this.$notify({
            message: error.message,
            type: "danger",
            timeout: 10000
          });
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>
