export const validateEmail = (email) => {
  if (!email.length) {
    return { valid: false, error: "This field is required." };
  }

  if (!email.match(/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(.\w{2,3})+$/)) {
    return { valid: false, error: "Please, enter a valid email." };
  }

  return { valid: true, error: null };
}

export const validatePassword = (password) => {
  if (!password.length) {
    return { valid: false, error: "This field is required." };
  }

  if (password.length < 8) {
    return { valid: false, error: "Password is too short." };
  }

  return { valid: true, error: null };
}

export const validatePhone = (phone) => {
  if (!phone.length) {
    return { valid: false, error: "This field is required." };
  }

  if (
    phone.length < 9 ||
    phone.length > 11 ||
    !phone.match(/(^0[7-9][0-1][0-9]{8}$)|(^[0-9]{9}$)/)
  ) {
    return { valid: false, error: "Please, enter a valid phone number." };
  }

  return { valid: true, error: null };
}

export const validateCode = (code) => {
  if (!code.length) {
    return { valid: false, error: "Tracking code is required." };
  } else if (code.length != 23) {
    return { valid: false, error: "A valid tracking code should be 23 characters long." };
  } else if (!code.match(/(^[0-9a-zA-Z]{5}-[0-9a-zA-Z]{5}-[0-9a-zA-Z]{5}-[0-9a-zA-Z]{5})/)) {
    return { valid: false, error: "Please, enter a valid code number." };
  }

  return { valid: true, error: null };
}
